import { writable } from 'svelte/store'

function random() {
    return Math.floor(Math.random() * 10 + 1)
}

export const FeedbackStore = writable([{
    id: 1,
    rating: random(),
    text: "Amazing text 1",
}, {
    id: 2,
    rating: random(),
    text: "Amazing text 2",
}, {
    id: 3,
    rating: random(),
    text: "Amazing text 3",
}, ])